import * as React from "react";
import { useRef } from "react";
import { motion } from "framer-motion";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'; //faHardHat, faWrench
import { faInfoCircle } from '@fortawesome/free-solid-svg-icons';
import "./Circle.scss";
//import '../../styles/main.scss';
import { Row } from "reactstrap";

export const Circle = () => {
  const constraintsRef = useRef(null);

  return (
    <div className='container-for-circle'>
      <motion.div className="container" ref={constraintsRef}>
        <motion.div className="item" drag dragConstraints={constraintsRef}>
          <p className='within-circle'>
            <p className='text'>To be updated</p>
          </p>
        </motion.div>
        <p className='text-below-circle'><FontAwesomeIcon className='icon' icon={faInfoCircle} /> Tap and drag the circle</p>
      </motion.div>
    </div>
  );
};

export default Circle;
