import React, { memo, useState } from 'react';
import { Button, Collapse } from 'reactstrap';
import ArrowImage from '../../media/down-blue.svg';
import './CustomDropdown.scss';


function CustomDropdown({ additionalHeader, header, children, isExpanded }) {
  const [isOpen, setIsOpen] = useState(isExpanded);
  const [isRender, setIsRender] = useState(isExpanded);
  const handleOpen = () => {
      setIsRender(true);
  };
  const handleClose = () => {
      setIsRender(false);
  };
  const handleClick = () => {
      setIsOpen(!isOpen);
  };

  return (
    <div className='custom-dropdown'>
      <div className={isOpen ? 'custom-dropdown-menu-open' : 'custom-dropdown-menu'}>
        {additionalHeader && <div>{additionalHeader}</div>}
        <div className='custom-dropdown-clickable' onClick={handleClick}>
            <div className={isOpen ? 'custom-dropdown-header-open' : 'custom-dropdown-header'}>{header}</div>
            <img
              style={
                isOpen
                  ? { right: '5px', display: 'inline', marginTop: '3px', marginRight: '6px', marginLeft: '6px', transform: 'rotate(270deg)' }
                  : { right: '5px', display: 'inline', marginTop: '3px', marginRight: '6px', marginLeft: '6px' }
              }
              src={ArrowImage}>
            </img>
            <div style={{ width: '100%' }}></div>
        </div>
      </div>
      <Collapse onEnter={handleOpen} onExited={handleClose} isOpen={isOpen}>
        {isRender && <div style={{ paddingBottom: '10px' }}>{children}</div>}
      </Collapse>
    </div>
  );
}

export default memo(CustomDropdown);
