import React, { Component } from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import '../../styles/main.scss';
import './modalPerson.css';
import PropTypes from 'prop-types';

class ModalPerson extends Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };
    this.toggle = this.toggle.bind(this);
  }
  toggle() { 
    this.setState( { modal: !this.state.modal } ); 
  }

  render() {
    return (
      <div>
        <p className='thin'>
          <span className='active' onClick={this.toggle}>
            {this.props.name}
          </span>
        </p>
        <Modal isOpen={this.state.modal} toggle={this.toggle} >
          <ModalHeader toggle={this.toggle}>
            {this.props.header}
          </ModalHeader>
          <ModalBody>
            <div className="card">
              <div className="top">
                <h2 className="name">{this.props.name}</h2>
                <img className="circle-img" src={this.props.img} alt="avatar_img" />
              </div>
              <div className="bottom">
                <p className="info">{this.props.tel}</p>
                <p className="info">{this.props.email}</p>
              </div>
            </div>
          </ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.toggle}>Cancel</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}

export default ModalPerson;