import React, { Component } from 'react';
import { motion } from 'framer-motion';
// import { Route, Switch } from 'react-router-dom'; import LikeDislike from '../LikeDislike/LikeDislike';
import ReactTooltip from 'react-tooltip';
import Circle from '../Motions/Circle';
import CustomDropdown from "../CustomDropdown/CustomDropdown";
import '../../styles/main.scss';

function About () {
  return (
    <div>
      <motion.div className='wrapper-middle-to-be-updated-motion'
        initial={{opacity: 0}}
        animate={{opacity: 1}}
        exit={{opacity: 0}}
      >
        <div> 
          <h1 className='how-it-started'>About us</h1>
          {/* <p className='thick'>To be updated soon</p> */}
          <Circle />
        </div>
        <div>
          <p></p>
          <CustomDropdown header="Find out more" isExpanded={false}>
            <div className='custon-dropdown'>
              <p>This section is to be updated soon.</p>
              <p>Stay tuned !</p>
              {/* <ul><li>1. a</li></ul> */}
            </div>
          </CustomDropdown>
        </div>
      </motion.div>
    </div>
  );
}

export default About;
