import React, { useState, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import './sidebar.scss';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faHome, faBook, faEnvelopeOpen } from '@fortawesome/free-solid-svg-icons';
import { Col, Row } from 'reactstrap';
// import $ from 'jquery';

function Sidebar() {
  const [sidebar, setSidebar] = useState(false);
      // const showSidebar = () => setSidebar(!sidebar);
  const node = useRef();
	const handleClick = (e) => {
		if (node.current.contains(e.target)) {
      // inside click
      setSidebar(!sidebar);
			return;
		}
		// outside click
		setSidebar(false);
	};

	useEffect(() => {
    document.addEventListener('mousedown', handleClick);
    document.addEventListener('pointerdown', handleClick);
    document.addEventListener('pointerup', handleClick);
		return () => {
      document.removeEventListener('mousedown', handleClick);
      document.removeEventListener('pointerdown', handleClick);
      document.removeEventListener('pointerup', handleClick);
		};
  }, []);
  
  return (
    <nav className={sidebar ? "sidebar active" : "sidebar"} ref={node}>
      <button className="sandwich" type="button" onClick={handleClick}>
        <div></div>
      </button>
      <ul onClick={handleClick}>
        {/* <li><Link to="/">Home</Link></li> <li><Link to="/about">About</Link></li> */}
        <li>
          <Link to="/gtd-react-app/">
            <Row>
              <Col className='nav-icon'><FontAwesomeIcon icon={faHome} /></Col>
              <Col className='nav-headers'>Home</Col>
            </Row>
          </Link>
        </li>
        <li>
          <Link to="/gtd-react-app/about">
            <Row>
              <Col className='nav-icon'><FontAwesomeIcon icon={faBook} /></Col> 
              <Col className='nav-headers'>About</Col>
            </Row>
          </Link>
        </li>
        <li>
          <Link to="/gtd-react-app/contact">
            <Row>
              <Col className='nav-icon'><FontAwesomeIcon icon={faEnvelopeOpen} /></Col> 
              <Col className='nav-headers'>Contact</Col>
            </Row>
          </Link>
        </li>
      </ul>
    </nav>
  );
}

export default Sidebar;